.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #212529;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.name {
  float: left;
  margin: 10px 0 0 0;
}

.high {
  margin: 20px 0 0 0;
  float: right;
}

.divide {
  margin: 20px 20px 0 20px;
}
.logo {
  margin-top: 10px;
}

.foot {
  margin: 0;
  padding: 0;
  font-size: small;
}

.title {
  border-bottom: 3px solid #212529;
  margin-bottom: 20px;
}

.high {
  color: #212529;
  text-decoration: none;
}

Link:visited.high {
  color: #476589;
  text-decoration: none;
}

Link:active.high {
  color: #a39990;
  text-decoration: none;
}

.main {
  float: left;
}

.connect {
  float: right;
}

.headline {
  border-bottom: 2px solid #212529;
  color: #212529
}

#stack {
  width: 95px;
  height: 95px;
  position: relative;
  bottom: 14px;
  left: 5px;  
}
#linked {
  margin-left: 10px
}

#git {
  margin-left: 20px;
}

.headshot {
  max-width: 30%;
  height: auto;
  float: left;
  margin: 5px 10px 10px 5px;
  border-radius: 4px;
}

header {
  border-bottom: 2px solid #212529;
  margin-bottom: 20px;
}

.copy {
  margin: 5px 5px;
  color: #212529
}

footer {
  clear: both;
  background-color: #a39990;
  text-align: center;
  border-top: 10px solid #212529;
  padding: 10px 0;
  border-radius: 4px;
}

body {
  background-color: #476589;
}

.content {
  background-color: #dde7f5;
  padding: 10px 15px 0 10px;
}

.content-main {
  padding-bottom: 20px;
  margin-bottom: 30px;
  border-radius: 4px;
}

.top {
  padding: 10px 0 20px 0;
  background-color: #dde7f5;
  margin-bottom: 30px;
  border-bottom: 5px solid #212529;
}

.tab {
  height: 40px;
  max-width: 120px;
  background-color: #a39990;
  text-align: center;
  position: relative;
  left: 15px;
}

#tab1 {
  border-top: 2px solid #212529;
  border-right: 1px solid #212529;
  border-left: 2px solid #212529;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 0px;
}

#tab2 {
  border-top: 2px solid #212529;
  border-right: 1px solid #212529;
  border-left: 1px solid #212529;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
}

#tab3 {
  border-top: 2px solid #212529;
  border-right: 2px solid #212529;
  border-left: 1px solid #212529;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 4px;
}

a:link.foot {
  color: #212529;
  text-decoration: none;
}

a:visited.foot {
  color: #476589;
  text-decoration: none;
}

.submit {
  background-color: #212529;
  border-color: #212529;
  float: right;
}

button.hover.submit {
  background-color: #a39990;
}

.contact {
  margin-bottom: 20px;
  max-width: 400px;
}

label {
  margin: 5px;
}

.projectImage {
  max-width: 280px;
  height: auto;
  margin: 5px;

}

.projectTitle {
  border-bottom: 2px solid #212529;
  margin-top: 5px;
}

.hangman {
  position: relative;
  left: 100px;
}


